/* ========================================================================== */
/* BASE STYLES */
/* ========================================================================== */
//@import "bootstrap/bootstrap.less";
@import "variables.less";
@import "normalize.less";
@import "lesshat.less";
@import "helpers.less";
@import "../../../node_modules/slick-carousel/slick/slick.less";
@import (inline) "lightbox.css";


main{
  min-height: 50vh;
}
.content{
  .screen(2px,@s1300,{
    overflow: hidden;
    width:100%;
  });
}
.header{
  background-image: url(../img/fon.jpg);
  background-position: center center;
  background-repeat:no-repeat;
  background-size:cover;
  &.mainpage{
    padding-bottom:130px;
    .screen(2px,@s1300,{
      padding-bottom:0;
    });
  }
  &-up{
    &_wrapper{
      .container;
      .flex(row,nowrap,flex-start,flex-start);
      .screen(2px,@s1300,{
        .flex(row,nowrap,space-between,center);
        padding:0;
      });
    }
    &_logo{
      .size(249px,210px);
      .gradient(to bottom,rgba(255, 255, 255, 0.2),transparent);
      .screen(2px,@s1300,{
        .size(70px,60px);
      });
      a{
        .flex(row,nowrap,center,center);
        height:100%;
        img{
          display: block;
          margin:0 auto;
          .size(133px,140px);
          .screen(2px,@s1300,{
            .size(35px,50px);
          });
        }
      }
    }
    &_block{
      width:~'calc(100% - 250px)';
      .screen(2px,@s1300,{
        width:auto;
      });
    }
  }
  &-top{
    .flex(row,nowrap,space-between,center);
    margin-top:15px;
    .screen(2px,@s1300,{
      display: block;
      margin-top:0;
    });
    &_menu{
      .flex(row,nowrap,flex-start,center);
      .reset-around;
      list-style: none;
      .screen(2px,@s1300,{
        display: block;
      });
      &>li{
        margin-left:30px;
        .screen(2px,@s1300,{
          margin-left:0;
          border-bottom:1px solid #e6e6e6;
        });
        &.deep{
          position: relative;
          .screen(2px,@s1300,{
            border-bottom:0;
            &>a{
              display: none;
            }
            .header-top_menuAdd{
              position: static;
              .reset-around;
              box-shadow: none;
              border-radius:0;
              li{
                border-bottom:1px solid #e6e6e6;
              }
              a{
                color: #4c4c4c;
                font-size: 15px;
                padding: 12px 15px;
                display: block;
                white-space: initial;
                margin-bottom:0;
                &:hover{
                  color:black;
                }
              }
            }
          });
          &.opened{
            &>a{
              color:white;
              &:after{
                .rotate(180deg);
                border-top-color:white;
              }
            }
          }
          &>a:after{
            content:'';
            border:5px solid transparent;
            border-top-color:#c2c2c2;
            display: inline-block;
            vertical-align: text-bottom;
            margin-left:5px;
            transform-origin:center 25%;
            transition:all 0.3s;
          }
        }
        &>a{
          color:#c2c2c2;
          font-size:14px;
          &:hover{
            color:white;
          }
          .screen(2px,@s1300,{
            color:#4c4c4c;
            font-size:15px;
            padding:12px 15px;
            display: block;
            &:hover{
              color:black;
            }
          });
        }
      }
      &Add{
        position: absolute;
        .reset-around;
        list-style: none;
        padding:15px 23px;
        border-radius:25px;
        background-color: #fff;
        box-shadow: 0px 0px 30px 6px rgba(207, 207, 207, 0.4);
        left:-23px;
        top:100%;
        margin-top:5px;
        z-index:2;
        li:last-child a{
          margin-bottom: 0;
        }
        a{
          white-space: nowrap;
          color:@color-base;
          font-size:14px;
          margin-bottom:8px;
          &:hover{
            color:#a5a5a5;
          }
        }
      }
    }
    &_reg{
      list-style: none;
      .reset-around;
      .flex();
      &:before{
        content:'';
        .size(27px);
        display: inline-block;
        background-image: url(../img/user.svg);
        background-repeat:no-repeat;
        background-position: center center;
        background-size:contain;
        .screen(2px,@s1300,{
          display: none;
        });
      }
      .screen(2px,@s1300,{
        display: block;
      });
      .header-top_menuAdd{
        a{
          color:@color-base;
          font-size:14px;
          margin-left:0;
          line-height: 22px;
          &:hover{
            color:#a5a5a5;
          }
        }
      }
      a{
        color:white;
        font-size:15px;
        margin-left:15px;
        cursor:pointer;
        line-height: 25px;
        &:hover{
          span{
            border-color:transparent;
          }
        }
        span{
          border-bottom:1px dotted #838383;
        }
        .screen(2px,@s1300,{
          color:#4c4c4c;
          font-size:15px;
          padding:12px 15px;
          display: block;
          margin:0;
          span{
            border-color:transparent;
          }
          &:hover{
            color:black;
          }
        });
      }
      li{
        .screen(2px,@s1300,{
          margin-left:0;
          border-bottom:1px solid #e6e6e6;
          &.deep{
            .screen(2px,@s1300,{
              border-bottom:0;
              &>a{
                display: none;
              }
              .header-top_menuAdd{
                position: static;
                .reset-around;
                box-shadow: none;
                border-radius:0;
                li{
                  border-bottom:1px solid #e6e6e6;
                }
                a{
                  color: #4c4c4c;
                  font-size: 15px;
                  padding: 12px 15px;
                  display: block;
                  white-space: initial;
                  margin-bottom:0;
                  &:hover{
                    color:black;
                  }
                }
              }
            });
          }
        });
        &.logged{
          position: relative;
        }
      }
    }
  }
  &-call{
    width:270px;
    .flex(row,nowrap,flex-start,flex-start);
    margin-top:15px;
    .img{
      .size(68px);
      background-repeat:no-repeat;
      background-position: center center;
      background-size:cover;
      border-radius:50%;
      position: relative;
    }
    .marker{
      border-radius:50%;
      .size(15px);
      background-color: #b7b7b7;
      border:2px solid white;
      position: absolute;
      top:2px;
      right:2px;
      &.active{
        background-color: #5eb978;
      }
    }
    .name{
      font-size:13px;
      color:#8d8d8d;
      font-style:italic;
      text-align: center;
    }
    &_right{
      color:white;
      margin-left:20px;
    }
    .screen(2px,@s1300,{
      margin-left:15px;
      margin-bottom:15px;
      &_right{
        color:black;
      }
      .name{
        color:black;
      }
    });
  }
  &-bottom{
    .flex(row,nowrap,space-between,center);
    margin-top:15px;
    .screen(2px,@s1300,{
      margin-bottom:25px;
      border-bottom:1px solid #e6e6e6;
    });
    .cons{
      margin:0 0 5px 0;
      white-space: nowrap;
    }
    .tel{
      font-size:20px;
      font-weight:600;
      line-height: 1.1;
      margin-bottom:5px;
      &:after{
        content:'';
        border:5px solid transparent;
        border-top-color:white;
        display: inline-block;
        vertical-align: middle;
        margin-left:5px;
        transform-origin:center ;
        transition:all 0.3s;
      }
      span{
        border-bottom:1px dotted #838383;
        line-height: 1;
        display: inline-block;
        cursor: pointer;
      }
    }
    .grafic{
      font-size:13px;
    }
  }
  &-cart{
    button{
      border:0;
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2), transparent);
      border-radius:25px;
      .reset-around;
      outline: none;
      padding:9px 35px 9px 25px;
      min-width: 190px;
      min-height: 50px;
      &.light{
        box-shadow: 0px 9px 21px 4px rgba(183, 17, 115, 0.49);
        background:#e6007e;
      }
    }
    .empty{
      .flex(row,nowrap,flex-start,center);
      color:white;
      svg{
        margin-right:15px;
      }
    }
    .in{
      .flex(row,nowrap,flex-start,center);
      color:white;
      span{
        font-weight:600;
      }
      .img{
        position: relative;
        svg{
          display: block;
          margin-right:15px;
        }
        .count{
          position: absolute;
          top:-7px;
          left:-5px;
          background-color: #343434;
          color:white;
          .size(21px);
          .flex(row,nowrap,center,center);
          border-radius:50%;
          font-size:11px;
        }
      }
    }
    .screen(2px,@s1300,{
      display: none;
    });
  }
  &-search{
    width:400px;
    position: relative;
    padding-left:30px;
    z-index:2;
    form{
      .flex;
      svg{
        position: absolute;
        top:18px;
        opacity: 0;
        cursor: pointer;
      }
    }
    input{
      width:100%;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 10px 3px rgba(190, 190, 190, 0.42), inset 0px 2px 5px 0px rgba(217, 217, 217, 0.4);
      border:0;
      .border-radius(25px,0,0,25px);
      height:50px;
      outline: none;
      .placeholder(#a1a1a1);
      padding:0 25px;
    }
    button{
      background: -moz-linear-gradient( 90deg, rgb(255,255,255) 0%, rgb(236,236,236) 0%, rgb(255,255,255) 100%);
      background: -webkit-linear-gradient( 90deg, rgb(255,255,255) 0%, rgb(236,236,236) 0%, rgb(255,255,255) 100%);
      background: -ms-linear-gradient( 90deg, rgb(255,255,255) 0%, rgb(236,236,236) 0%, rgb(255,255,255) 100%);
      background: linear-gradient( to top, rgb(255,255,255) 0%, rgb(236,236,236) 0%, rgb(255,255,255) 100%);
      box-shadow: 0px 0px 10px 3px rgba(190, 190, 190, 0.4);
      height: 50px;
      border:0;
      .border-radius(0,25px,25px,0);
      padding:0 25px;
      color:black;
      &:hover{
        background: #fff;
      }
    }
    &_wrapper{
      position: relative;
      .screen(2px,@s1300,{
        display: none;
      });
    }
    &_hiddenWrap{
      position: absolute;
      top:25px;
      left:30px;
      padding:45px 0px 25px 25px;
      z-index:1;
      background-color: #fff;
      .border-radius(0,0,25px,25px);
      width: ~'calc(100% - 30px)';
      box-shadow: 0px 0px 16px 6px rgba(190, 190, 190, 0.42), inset 0px 2px 5px 0px rgba(217, 217, 217, 0.4);
    }
    &_hidden{
      max-height: 200px;
      overflow: auto;
      padding-right:25px;
      .link{
        margin-bottom:10px;
      }
      a{
        line-height: 22px;
        color:@color-base;
        &:hover{
          color:@color-pink;
        }
      }
    }
    &_title{
      .screen(@s1300,90000px,{
        display:none;
      });
    }
    &_undertitle{
      .screen(@s1300,900000px,{
        display:none;
      });
    }
  }
  &-mobile{
    .flex(row,nowrap,flex-end,center);
    .screen(@s1300,90000px,{
      display: none;
    });
    svg{
      display: block;
    }
    &_search{
      margin-right:35px;
    }
    &_cart{
      position: relative;
      margin-right:60px;
      .count{
        position: absolute;
        top:-5px;
        right:-5px;
        border-radius:50%;
        .size(22px);
        background-color: @color-pink;
        color:@color-base;
        .flex(row,nowrap,center,center);
        font-size:11px;
      }
    }
    &_button{
      background-color: @color-pink;
      .size(60px);
      .flex(row,nowrap,center,center);
    }
  }
  &-desktop{
    &_m{
      display: none;
    }
    .screen(2px,@s1300,{
      display: none;
      &_m{
        display: block;
        .title-m{
          height: 50px;
          .flex(row,nowrap,space-between,center);
          padding:0 15px;
          svg{
            display: block;
          }
        }
        .catalog-m{
          height: 50px;
          padding:0 15px;
          background-color: #f2f2f2;
          .flex(row,nowrap,flex-start,center);
          border-top:1px solid #e6e6e6;
          border-bottom:1px solid #e6e6e6;
          svg{
            margin-right:25px;
          }
          span{
            color:black;
          }
        }
        .screen(@s1300,900000px,{
          display: none;
        });
      }
      &_wrapper{
        overflow-x: hidden;
        overflow-y: auto;
        height:100%;
      }
      &.opened{
        display: block;
        position: fixed;
        width:100vw;
        top:0;
        left:0;
        height: 100vh;
        background-color: #fff;
        z-index:100;
        overflow: hidden;
        .translate(100%);
      }
      &.search_opened{
        display: block;
        .header-desktop_m,
        .header-top,
        .header-cart,
        .header-call{
          display: none;
        }
        .header-bottom{
          margin:0;
          border:0;
        }
        .header-search_wrapper{
          position: fixed;
          top:0;
          left:0;
          width:100vw;
          height: 100vh;
          background-color: #fff;
          display: block;
          z-index:6;
          .translateY(-100%);
        }
        .header-search_title{
          height: 50px;
          border-bottom:1px solid #e6e6e6;
          margin-bottom:15px;
          padding:13px 0;
          position: relative;
          text-align: center;
          svg{
            fill:@color-pink;
            position: absolute;
            top:15px;
            right:15px;
            display: block;
          }
        }
        .header-search_undertitle{
          font-size:15px;
          color:#4c4c4c;
          margin:0 0 10px 0;
        }
        .header-search{
          padding:0 15px;
          width:100%;
          input{
            box-shadow:none;
            border:1px solid #e6e6e6;
            border-radius:25px;
            .placeholder(transparent);
            padding-right:50px;
          }
          form{
            position: relative;
          }
          button{
            box-shadow: none;
            border:0;
            background: none;
            position: absolute;
            top:0;
            right:20px;
            padding:0;
            svg{
              opacity: 1;
              position: static;
              display: block;
            }
          }
        }
        .header-search_hiddenWrap{
          position: static;
          padding:10px 15px 10px 15px ;
          margin-top:25px;
          width:100%;
          background: white;
          box-shadow: none;
          overflow: auto;
          max-height: ~'calc(100% - 180px)';
          .link{
            a{
              color:#737373;
            }
          }
        }
      }
    });
  }
  .screen(2px,@s1300,{
    height: 60px;
  });
}
.lang{
  .screen(2px,@s1300,{
    display: none;
  });
  a{
    color:#c2c2c2;
    font-size:14px;
    text-transform: uppercase;
    &.current{
      color:white;
    }
  }
  &-m{
    display: block;
    a{
      color:#737373;
      margin-right:25px;
      text-transform: capitalize;
      font-size: 15px;
      &.current{
        color:black;
      }
    }
  }
}
.nav{
  .container;
  position: relative;
  display: none;
  &.opened{
    display: block;
    z-index:2;
    .nav-ul{
      position: absolute;
      top:0;
      left:15px;
      width:~'calc(100% - 30px)';
    }
  }
  &.mainpage{
    display: block;
    min-height: 130px;
    .screen(2px,@s1300,{
      //display: none;
    });
    .nav-ul{
      position: absolute;
      top:-130px;
      width:~'calc(100% - 30px)';
    }
  }
  &-ul{
    .reset-around;
    list-style: none;
    .flex(row,wrap,flex-start,stretch);
    position: relative;
    &>li{
      height:130px;
      width:~'calc(20% + 1px)';
      border:1px solid #dcdcdc;
      .flex(row,wrap,flex-start,flex-start);
      background: linear-gradient( to top, rgb(247,247,247) 0%, rgb(255,255,255) 100%);
      cursor:pointer;
      margin-bottom:-1px;
      margin-right:-1px;
      padding:15px;
      .img{
        margin-top:15px;
        width:100%;
        text-align: center;
        svg{
          .size(35px);
          .cls-11{
            filter:url(#filterPink);
          }
        }
      }
      .link{
        text-align:center;
        line-height:20px;
        width:100%;
        height: 40px;
        overflow: hidden;
      }
      .line{
        height: 0px;
        width:100%;
        position: relative;
        align-self: flex-end;
        &:after{
          content:'';
          position: absolute;
          z-index:2;
          width:~'calc(100% + 30px)';
          height: 30px;
          background-color: white;
          left:-15px;
          bottom: -25px;
          z-index: 5;
          display: none;
        }
      }
      &.opened{
        background: #fff;
        border-color:#fff;
        box-shadow: 0px 0px 30px 0px rgba(207, 207, 207, 0.4);
        z-index:1;
        .img{
          svg {
            fill:#292929;
            .cls-11{
              filter:url(#filterGray);
            }
          }
        }
        .inner{
          display: block;
        }
        .line:after{
          display: block;
        }
      }
    }
    .screen(2px,@s1300,{
      display: block;
      position: static!important;
      width: ~'calc(100% + 30px)'!important;
      margin-left:-15px;
      margin-right:-15px;
      &>li{
        height:auto;
        width:100%;
        border:1px solid #dcdcdc;
        border-left-width: 0;
        border-right-width: 0;
        border-top-width: 0;
        display: block;
        background: #f2f2f2;
        cursor:pointer;
        margin-bottom:0;
        margin-right:0;
        padding:15px;
        text-align:left;
        .img{
          display: none;
        }
        .link{
          text-align:left;
          line-height:20px;
          width:100%;
          height: auto;
          overflow: hidden;
        }
      }
    });
  }
  &-wrapper{
    .screen(2px,@s1300,{
      display: block;
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
      width: 100vw;
      background: white;
      z-index: 101;
      .translateX(100%);
      overflow-y: auto;
      overflow-x: hidden;
    });
    &.opened{
      .nav{
        display: block;
      }
    }
  }
  &-in{
    &_mobile{
      .screen(@s1300,900000px,{display:none;});
      .flex(row,nowrap,space-between,center);
      height: 50px;
      border-bottom:1px solid #dcdcdc;
      width:~'calc(100% + 30px)';
      margin:0 -15px;
      padding:0 15px;
      .back{
        svg{
          fill:@color-pink;
          .rotate(180deg);
          display: block;
        }
      }
      .close{
        svg{
          fill:@color-pink;
          display: block;
        }
      }
    }
  }
}
.menu{
  .container;
  .flex(row,nowrap,flex-start,flex-start);
  .screen(2px,@s900,{
    display: none;
  });
  &-wrapper{

    background-color: #f9f9f9;
  }
  &-catalog{
    width:249px;
    background-color: @color-pink;
    cursor:pointer;
    .flex(row,nowrap,center,center);
    height:60px;
    .screen(2px,@s1300,{
      display: none;
    });
    svg{
      fill:white;
      display: block;
      margin-right:15px;
    }
    span{
      color:white;
      font-size:17px;
    }
  }
}
.inner{
  display: none;
  position: absolute;
  width:~'calc(100% + 1px)';
  z-index:3;
  background-color: #fff;
  left:0;
  padding:30px 15px 45px 50px;
  cursor: default;
  box-shadow: 0px 0px 30px 0px rgba(207, 207, 207, 0.4);
  .title{
    .flex(row,nowrap,space-between,center);
    margin-bottom:25px;
    svg{
      .size(20px);
      cursor:pointer;
      display:block;
    }
    h4{
      margin:0;
      font-size:17px;
      .screen(2px,@s1300,{
        font-weight: 400;
        font-size:15px;
      });
    }
  }
  &>ul{
    .reset-around;
    list-style:none;
    //.flex(row,wrap,flex-start,flex-start);
    columns:4;
    &>li{
      //width:19%;
      //min-width:290px;
      margin-bottom: 10px;
      padding:0 10px 0 25px;
      position: relative;
      &:before{
        content:'';
        .size(7px);
        background-color: @color-pink;
        position: absolute;
        top:7px;
        left:0;
        .rotate(45deg);
      }
      &>a{
        color:@color-base;
        &:hover{
          color:#989898;
        }
      }
    }
  }
  &.mobileOpened{
    position: fixed;
    top:0;
    left:0;
    width:100vw;
    height: 100vh;
    background-color: #fff;
    display: block;
    z-index:3;
    padding:0;
    box-shadow: none;
    .translateX(100%);
    overflow-x: hidden;
    overflow-y: auto;
    &>.title{
      .screen(@s1300,900000px,{display:none;});
      .flex(row,nowrap,space-between,center);
      height: 50px;
      border-bottom:1px solid #dcdcdc;
      width:100%;
      padding:0 15px;
      margin-bottom:0;
      .back{
        svg{
          fill:@color-pink;
          .rotate(180deg);
          display: block;
        }
      }
      .close{
