      &.right .block{
        padding-left:120px;
        min-height: 100px;
        position: relative;
        .img{
          .size(100px);
          border-radius:50%;
          background-repeat:no-repeat;
          background-size:cover;
          background-position: center center;
          position: absolute;
          left:0;
          top:0;
          box-shadow: 3px 4px 17px -10px black;
          a{
            display: block;
            height:100%;
          }
        }
      }
      .block{
        margin-bottom:25px;
        .date{
          font-size:14px;
          color:#8a8a8a;
          margin-bottom:5px;
        }
        .title{
          a{
            color:black;
            font-size:17px;
            font-weight:700;
            margin-bottom:5px;
            &:hover{
              color:#989898;
            }
          }
        }
        .text{
          line-height: 20px;
          color:black;
        }
      }
      .arrow{
        a{
          color:@color-pink;
          &:hover{
            span{
              border-color:transparent;
            }
          }
          b{
            vertical-align: text-bottom;
          }
          span{
            border-bottom:1px solid#f6bfdd;
          }
        }
      }
    }
    &_title{
      font-size:22px;
      font-family: @font-lora;
      margin-bottom:20px;
      a{
        color:@color-pink;
        border-bottom:1px solid #f6bfdd;
        line-height: 20px;
        &:hover{
          border-color:transparent;
        }
      }
    }
    &_wrapper{
      background-color: #f9f9f9;
      padding:30px 0;
      margin-bottom:25px;
    }
    .screen(2px,1000px,{
      display: block;
      &_block{
        width:100%;
        padding-right:0!important;
        padding-left:0!important;
        &:first-child{
          margin-bottom:50px;
        }
        .text{
          display: none;
        }
      }
    });
  }
  &-otz{
    .container;
    position: relative;
    margin-bottom:50px;
    &_title{
      font-size:22px;
      font-family:@font-lora;
      text-align:center;
      line-height: 20px;
      margin-bottom:25px;
      padding-top:15px;
      .screen(2px,768px,{
        text-align: left;
        max-width:~'calc(100% - 100px)';
      });
    }
    &_arrowWrapper{
      position: absolute;
      top:0;
      right:15px;
      height:40px;
      width:90px;
      .screen(2px,768px,{
        position: relative;
        float: right;
        right:0;
      });
    }
    &_arrow{
      position: absolute;
      top:7px;
      border:1px solid #d7d7d7;
      border-radius:50%;
      .size(40px);
      .flex(row,nowrap,center,center);
      cursor: pointer;
      background-color: #fff;
      z-index:5;
      &:after{
        content:'';
        border-bottom:1px solid black;
        border-right:1px solid black;
        .size(10px);
        display: block;
        .rotate(135deg);
        position: relative;
        left:2px;
      }
      &.next{
        right:0;
        &:after{
          .rotate(-45deg);
          left:-2px;
        }
      }
    }
    .slick{
      &-slider{
        height: 230px;
        overflow: hidden;
        .screen(2px,@s1300,{
          height: auto;
          overflow: visible;
        });
      }
      &-center{
        box-shadow:0px 0px 22px -11px black;
      }
      &-slide{
        padding:25px 35px;
        outline:none;
        min-height: 230px;
        .screen(2px,768px,{
          padding:15px 0;
        });
        .block{
          padding-left: 140px;
          position: relative;
          .img{
            background-repeat:no-repeat;
            background-position: center center;
            background-size:cover;
            .size(121px,127px);
            position: absolute;
            left:0;
            top:0;
            &-in{
              height: 100%;
              background-image: url(../img/fb-ramka.png);
              background-repeat:no-repeat;
              background-position: 0 0;
              a{
                display: block;
                height:100%;
              }
            }
          }
          .text{
            color:black;
            line-height: 20px;
            margin-bottom:15px;
            max-height: 164px;
            overflow: hidden;
          }
          .author{
            font-style: italic;
            color:#8a8a8a;
          }
        }
      }
    }
  }
}
.catalog{
  &-mobile{
    &_back{
      height:50px;
      background-color: white;
      border-bottom:1px solid #e6e6e6;
      width:~'calc(100% + 30px)';
      margin: 0 -15px;
      padding:0 15px;
      .flex(row,nowrap,flex-start,center);
      .screen(@s1300,90000px,{display: none});
      svg{
        fill:@color-pink;
        .rotate(180deg);
        margin-right:15px;
      }
      span{
        color: black;
        font-size:14px;
        line-height: 1.1;
        max-height:30px;
        overflow: hidden;
        width:~'calc(100% - 35px)';
      }
    }
  }
}
.h1-title{
  padding:25px 0 15px 0;
  h1{
    font-size: 35px;
    font-family: @font-lora;
    font-weight:400;
    line-height: 1.1;
    .reset-around;
  }
}

.catalog{
  &-wrapper{
    .flex(row,nowrap,flex-start,stretch);
  }
  &-left{
    &_wrapper{
      width:20%;
      .screen(2px,@s1170,{
        display: none;
      });
    }
  }
  &-right{
    padding-left:25px;
    .screen(2px,@s1170,{
      padding-left:0;
    });
    &_wrapper{
      width:80%;
      .screen(2px,@s1170,{
        width:100%;
      });
    }
  }
  &-filter{
    margin-bottom:50px;
    &_block{
      border-bottom:1px solid #f2f2f2;
      .slider-input{
        .flex(row,nowrap,space-between,center);
        margin:25px 0;
        color:#4c4c4c;
        input{
          border:1px solid #dcdcdc;
          height: 40px;
          border-radius:5px;
          background: #fff;
          padding:0 5px;
          text-align: center;
          width:35%;
        }
      }
      .vue{
        &-slider{
          background-color: #d8d8d8!important;
          border-radius:0!important;
          &-process{
            background-color: #4d4d4d!important;
          }
          &-dot{
            border-radius: 50%;
            background: linear-gradient( to top, rgb(255,255,255) 0%, rgb(236,236,236) 0%, rgb(255,255,255) 100%)!important;
            box-shadow: 0px 0px 3px 0px rgba(190, 190, 190, 0.4)!important;
            width: 19px;
            height: 19px;
            .flex(row,nowrap,center,center);
            &:after{
              content:'';
              border-radius: 50%;
              background-color: rgb(217, 217, 217);
              box-shadow: inset 0px 2px 0px 0px rgba(0, 0, 0, 0.1);
              width: 9px;
              height: 9px;
            }
          }
        }
      }
      .title{
        color:#4c4c4c;
        font-size:17px;
        font-weight:700;
        margin:15px 0;
        padding-left:35px;
        position: relative;
        min-height: 23px;
        line-height: 23px;
        cursor: pointer;
        &.hidden{
          &:before{
            .rotate(180deg);
          }
        }
        &:after{
          content:'';
          position: absolute;
          top:0;
          left:0;
          .size(23px);
          border:1px solid #d7d7d7;
          border-radius:50%;
        }
        &:before{
          content:'';
          position: absolute;
          top:9px;
          left:7px;
          .size(9px,5px);
          .rotate(0deg);
          transition:transform 0.4s;
          background-image: url(../img/ar-filter.png);
          background-repeat:no-repeat;
          background-position: center center;
        }
      }
      .color{
        margin-right:10px;
        margin-bottom:10px;
        label{
          display: inline-block;
          border:2px solid transparent;
          border-radius:50%;
          span{
            display: block;
            .size(25px);
            border-radius:50%;
            cursor: pointer;
            border:2px solid transparent;
          }
          &:hover {
            box-shadow: 0px 1px 4px 0px rgba(0, 0, 0, 0.17);
          }
        }
        input{
          &:checked ~ label{
            border:2px solid #e5e5e5;
            span{
              border-color:white!important;
            }
          }
        }
        &-wrapper{
          .flex(row,wrap,flex-start,center);
        }
      }
      .simple{
        label{
          padding-left:50px;
          position: relative;
          display: block;
          line-height: 23px;
          margin-bottom:15px;
          cursor: pointer;
          min-height: 23px;
          &:hover{
            &:before{
              border-color:black;
            }
          }
          &:before{
            content:'';
            .size(23px);
            border-radius:5px;
            border:1px solid #b6b6b6;
            position: absolute;
            left:0;
            top:0;
            display: block;
          }
        }
        input:checked ~ label{
          &:before{
            background-color: #4d4d4d;
            border-color:#4d4d4d;
          }
          &:hover{
            &:before{
              border-color:#4d4d4d;
            }
          }
          &:after{
            content:'';
            .size(23px);
            position: absolute;
            left:0;
            top:0;
            background-image: url(../img/check-mark.svg);
            background-repeat:no-repeat;
            background-position: center center;
            fill:white!important;
            background-size:13px;
          }
        }
        &-wrapper{
          padding:15px 0;
        }
      }
    }
    &_reset{
      color:#979797;
      font-size:14px;
      border-bottom:1px solid #f2f2f2;
      height: 0px;
      overflow: hidden;
      visibility: hidden;
      .flex(row,nowrap,flex-start,center);
      transition:all 0.3s;
      &.opened{
        height: 60px;
        visibility: visible;
      }
      span{
        border-bottom:1px dotted #ccc;
        cursor: pointer;
        line-height:1;
      }
      svg{
        cursor: pointer;
        margin-right:5px;
      }
    }
  }
  &-sort{
    &_wrapper{
      .flex(row,nowrap,space-between,center);
      .screen(@s1170,900000px,{
        height:60px;
      });
    }
    &_right{
      .flex(row,nowrap,flex-end,center);
      .screen(2px,@s1170,{display: none});
      span{
        color:#9e9e9e;
      }
      .switcher{
        border-radius:5px;
        .size(52px,43px);
        border:1px solid #ebebeb;
        .flex(row,nowrap,center,center);
        cursor:pointer;
        margin-left:15px;
        svg{
          fill:#999999;
        }
        &.active{
          box-shadow: 0px 1px 5px 0px rgba(230, 230, 230, 0.6);
          svg{
            fill:#4d4d4d;
          }
        }
      }
    }
    &_left{
      .flex(row,nowrap,flex-start,center);
      .screen(2px,@s1170,{display: none});
      span{
        color:#9e9e9e;
      }
      .item{
        cursor: pointer;
        margin-left:15px;
        padding:2px 15px;
        color:#4c4c4c;
        &.active{
          background-color: #f9f9f9;
        }
        &.down:after{
          content:'';
          border:5px solid transparent;
          border-top-color:#4c4c4c;
          display: inline-block;
          margin-left:8px;
          position: relative;
          top:3px;
        }
        &.up:after{
          content:'';
          border:5px solid transparent;
          border-bottom-color:#4c4c4c;
          display: inline-block;
          margin-left:8px;
          position: relative;
          top:-3px;
        }
      }
    }
    &_mobile{
      .screen(@s1170,90000px,{display: none});
      .flex(row,nowrap,space-between,center);
      width:100%;
    }
  }
  &-products{
    .flex(row,wrap,flex-start,flex-start);
    width:~'calc(100% + 20px)';
    margin:0 -10px;
    .product{
      width:~'calc(25% - 20px)';
      max-width:100%;
      margin:0 10px;
      margin-bottom: 10px;
    }
    &_wrapper{
      margin:15px 0 35px 0;
    }
    &.list{
      .screen(@s1170,90000px,{
        width:100%;
        margin:0;
        .product{
          width:100%;
          max-width:100%;
          .flex(row,nowrap,space-between,stretch);
          padding:15px 10px;
          margin:0;
          margin-bottom:15px;
          .img{
            width:215px;
          }
          .wrapper{
            width:~'calc(100% - 240px)';
            .flex(row,wrap,flex-start,flex-start);
            &>div{
              width:100%;
            }
            .undertitle{
              display: block;
              margin-bottom:10px;
              .editor{
                line-height:25px;
                color:#737373;
              }
            }
            .title{
              height: auto;
              overflow: visible;
            }
            .kod{
              order:-1;
              line-height: 25px;
              margin-top:10px;
              opacity: 1;
            }
            .price {
              align-self: flex-end;
              width: auto;
              margin-bottom: 15px;
            }
            .button{
              width:auto;
              margin-bottom:0;
              align-self: flex-end;
              margin-left:30px;
            }
          }
        }
      });
    }
  }
}